import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import Sidebar from "react-sidebar";
import settingsGear from './baseline_settings_white_18dp.png'
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
import ButtonToolbar from 'react-bootstrap/ButtonToolbar'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import { Component } from "react";
import Switch from "react-switch";
import Card from "react-bootstrap/Card";
const mql = window.matchMedia(`(min-width: 800px)`);

var curPage = 1;

class Home extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            sidebarDocked: mql.matches,
            sidebarOpen: false,
            modalShow: false,
            header: "Calc"
        };
        this.mediaQueryChanged = this.mediaQueryChanged.bind(this);
        this.onSetSidebarOpen = this.onSetSidebarOpen.bind(this);
        this.changeHeader = this.changeHeader.bind(this);
    }
    componentWillMount(){
        mql.addListener(this.mediaQueryChanged);
    }
    componentWillUnmount() {
        this.state.mql.removeListener(this.mediaQueryChanged);
    }
    onSetSidebarOpen(open) {
        this.setState({sidebarOpen: open});
    }
    mediaQueryChanged() {
        this.setState({sidebarDocked: mql.matches, sidebarOpen: false});
    }
    changeHeader(string){
        this.setState({header: string});
        switch (string) {
            case "Calc" : curPage = 1; break;
            case "Summon Simulator" : curPage = 2; break;
            case "About" : curPage = 3; break;
            case "Contact" : curPage = 4; break;
            default : curPage = 1;
        }
    }
    render() {
        let modalClose = () => this.setState({ modalShow: false });
        return (
            <Sidebar
                sidebar={
                    <div>
                        <div className={"upperBar"}>
                            <text id = "SBheader">Menu</text>
                        </div>
                            <div id = "barcont">
                                <ul id={"vert"}>
                                    <li className={"verticalItem"}><a className={'verticalText'} href = "#calc" onClick={() => this.changeHeader("Calc")}><b>Calc</b></a></li>
                                    <li className={"verticalItem"}><a className={'verticalText'} href = "#summon" onClick={() => this.changeHeader("Summon Simulator")}><b>Simulator</b></a></li>
                                    <li className={"verticalItem"}><a className={'verticalText'} href = "#about" onClick={() => this.changeHeader("About")}><b>About</b></a></li>
                                    <li className={"verticalItem"}><a className={'verticalText'} href = "#contact" onClick={() => this.changeHeader("Contact")}><b>Contact</b></a></li>
                                </ul>
                            </div>
                    </div>
                }
                open={this.state.sidebarOpen}
                docked={this.state.sidebarDocked}
                onSetOpen={this.onSetSidebarOpen}
                styles={{
                    root: {
                        position: "absolute",
                        top: 0,
                        left: 0,
                        right: 0,
                        bottom: 0,
                        overflow: "hidden"
                    },
                    sidebar: {
                        zIndex: 2,
                        position: "absolute",
                        top: 0,
                        bottom: 0,
                        transition: "transform .3s ease-out",
                        WebkitTransition: "-webkit-transform .3s ease-out",
                        willChange: "transform",
                        overflowY: "auto"
                    },
                    content: {
                        position: "absolute",
                        top: 0,
                        left: 0,
                        right: 0,
                        bottom: 0,
                        overflowY: "auto",
                        WebkitOverflowScrolling: "touch",
                        transition: "left .3s ease-out, right .3s ease-out"
                    },
                    overlay: {
                        zIndex: 1,
                        position: "fixed",
                        top: 0,
                        left: 0,
                        right: 0,
                        bottom: 0,
                        opacity: 0,
                        visibility: "hidden",
                        transition: "opacity .3s ease-out, visibility .3s ease-out",
                        backgroundColor: "rgba(0,0,0,0)"
                    },
                    dragHandle: {
                        zIndex: 1,
                        position: "fixed",
                        top: 0,
                        bottom: 0
                    }
                }}
            >
                <script src="https://unpkg.com/react/umd/react.production.min.js" crossOrigin/>

                <script
                    src="https://unpkg.com/react-dom/umd/react-dom.production.min.js"
                    crossOrigin
                />

                <script
                    src="https://unpkg.com/react-bootstrap@next/dist/react-bootstrap.min.js"
                    crossOrigin
                />

                <script>var Alert = ReactBootstrap.Alert;</script>
                <link
                    rel="stylesheet"
                    href="https://maxcdn.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css"
                    integrity="sha384-ggOyR0iXCbMQv3Xipma34MD+dH/1fQ784/j6cY/iJTQUOhcWr7x9JvoRxT2MZw1T"
                    crossOrigin="anonymous"
                />
                <div className={"upperBar"}>
                    <text id='SBbutton' onClick={() => this.onSetSidebarOpen(true)}>
                        ≡ {this.state.header}
                    </text>
                    <img id = 'settingsGear' src={settingsGear} alt="Logo" onClick={() => this.setState({ modalShow: true })}/>
                </div>
                <div className={"content"}>
                    <Content/>
                    <ButtonToolbar>
                        <MydModalWithGrid show={this.state.modalShow} onHide={modalClose} />
                    </ButtonToolbar>

                </div>
            </Sidebar>
        );
    }
}

class MydModalWithGrid extends React.Component {
    render() {
        return(
            <Modal {...this.props}
                   dialogClassName="modal-90w"
                   aria-labelledby="contained-modal-title-vcenter">
                <Modal.Header closeButton>
                    <Modal.Title id="contained-modal-title-vcenter">
                        Options
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Container>
                        <Col className = "show-grid">
                            <Row xs={12} md={8}>
                                <text>Option</text><SwitchExample/>
                            </Row>
                        </Col>
                    </Container>
                </Modal.Body>
                <Modal.Footer>
                    <Button onClick={this.props.onHide}>Close</Button>
                </Modal.Footer>
            </Modal>
        );
    }
}
class Content extends React.Component{
    render() {
        switch (curPage) {
            case 1: return(
                <UnitBuilder/>
            );
            case 2: return(
                <Test/>
            );
            case 3: return(
                <text className = 'content'>About</text>
            );
            case 4: return(
                <text className = 'content'>Contact</text>
            );
            default: return(
                <text className = 'content'>{curPage}</text>
            )
        }

    }
}

class UnitBuilder extends React.Component{
    constructor() {
        super();
        this.state = { button : 1};
        this.incrementClicked = this.incrementClicked.bind(this);
        this.resetClicked = this.resetClicked.bind(this);
    }
    incrementClicked(){
        curPage += 1;
        this.setState({button : curPage});
    }
    resetClicked(){
        curPage = 1;
        this.setState({button: curPage});
    }
    render() {
        return(
            <div>
                <div>
                    <text>{curPage}</text><nl/>
                </div>
                <div>
                    <Button
                        variant={"primary"}
                        onClick={() => this.incrementClicked()}
                        size={"sm"}
                    >+1</Button>
                    <Button
                        variant={"danger"}
                        onClick={() => this.resetClicked()}
                        size={"sm"}
                    >reset</Button>
                </div>
            </div>
        )
    }
}
class Test extends React.Component{
    render(){
        return(
            <div>
                <Row>
                    <Col>
                        <Container className={"container"}>
                            <text>This is a column</text>
                            <br/>
                            <text>This is a column</text>
                        </Container>
                    </Col>
                    <Col>
                        <Container className={"container"}>
                            <Card>
                                <Row>
                                    <Col>
                                        <img className={"image"} height="75" width="75" src="https://via.placeholder.com/300" alt={"alt"}/>
                                    </Col>
                                    <Col xl={7} lg={6} md={6} sm={6} xs={6}>
                                        <Container>
                                            <Card.Text className = {"image"}>Name</Card.Text> <br/>
                                            <Card.Text className = {"image"}>sub-Name</Card.Text>
                                        </Container>
                                    </Col>
                                    <Col>
                                        <Container>
                                            <Button variant={"primary"} size={"sm"}>Select</Button>
                                        </Container>

                                    </Col>
                                </Row>
                            </Card>
                        </Container>
                    </Col>
                </Row>
            </div>
        )
    }
}
class SwitchExample extends Component {
    constructor() {
        super();
        this.state = { checked: true };
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(checked) {
        this.setState({ checked });
    }

    render() {
        return (
            <label>
                <Switch
                    checked={this.state.checked}
                    onChange={this.handleChange}
                    onColor="#86d3ff"
                    onHandleColor="#2693e6"
                    handleDiameter={30}
                    uncheckedIcon={false}
                    checkedIcon={false}
                    boxShadow="0px 1px 5px rgba(0, 0, 0, 0.6)"
                    activeBoxShadow="0px 0px 1px 10px rgba(0, 0, 0, 0.2)"
                    height={20}
                    width={48}
                    id="material-switch"
                />
            </label>
        );
    }
}

ReactDOM.render(<Home/>, document.getElementById("root"));